  display: inline-flex;/*UC*/
  display: flex;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  width: 100%;
}
.sort-title{
  height: 35px;
  line-height: 35px;
  position: relative;
  text-align: center;
